.mdl-container{position:absolute;top:0;left:0;height:100%;width:100%}.mdl-container .mdl-overlay{position:fixed;padding:0;margin:0;height:100%;width:100%;z-index:1;background:#373a47;animation:a .5s cubic-bezier(.165,.84,.44,1) forwards}.mdl-container .mdl{position:absolute;top:50%;left:50%;-ms-transform:translate(-50%,-50%);transform:translate(-50%,-50%);padding:.5rem;width:45%;border-radius:3px;background:#fff;z-index:2;overflow:auto;animation:c .5s cubic-bezier(.165,.84,.44,1) forwards}.mdl-container.out .mdl-overlay{animation:b .5s cubic-bezier(.165,.84,.44,1) forwards}.mdl-container.out .mdl{animation:d .5s cubic-bezier(.165,.84,.44,1) forwards}@media only screen and (max-width:1366px){.mdl-container .mdl{width:80%;height:45%}}@media only screen and (max-height:1229px){.mdl-container .mdl{height:45%}}@keyframes a{0%{opacity:0}to{opacity:1}}@keyframes b{0%{opacity:1}to{opacity:0}}@keyframes c{0%{transform:translate(-50%,300%)}to{transform:translate(-50%,-50%)}}@keyframes d{0%{transform:translate(-50%,-50%)}to{transform:translate(-50%,-300%)}}